import React, { useEffect, useRef } from 'react'
import './Banner.styl'

export default function Banner() {

    let num = useRef(0);//key



    //
    function showButton() {
        const buttons = document.querySelectorAll("#buttons span") as NodeListOf<HTMLElement>;
        //
        for (let i = 0; i < buttons.length; i++) {
            if (buttons[i].className === "on") {
                buttons[i].className = "";
            }
        }
        //
        buttons[num.current].className = "on";
    }
    useEffect(() => {
        const imgsList = document.getElementById("list") as HTMLElement; //
        const screenWidth = document.body.offsetWidth; //
        const listWidth = 3 * screenWidth; //

        imgsList.style.width = listWidth.toString() + "px"; //
        //
        (function () {
            const imgs = document.querySelectorAll("#list img") as NodeListOf<HTMLElement>;
            for (let i = 0; i < imgs.length; i++) {
                imgs[i].style.width = screenWidth.toString() + "px";
            }
        })()
        
        const timer = setInterval(() => {
            num.current++;
            if (num.current === 3) {
                num.current = 0;
            }
            const offSetWidth = -num.current * screenWidth; //
            imgsList.style.left = offSetWidth.toString() + "px"; //
            showButton(); //
        }, 3000);
        //
        return () => {
            clearInterval(timer);
        }

    }, [])

    return (
        <div id="container">
            <div id="list" >
                <img src={require('../../assets/images/home_lunbo1.png')} key="0" alt="" />
                <img src={require('../../assets/images/home_lunbo2.png')} key="1" alt="" />
                <img src={require('../../assets/images/home_lunbo3.png')} key="2" alt="" />
            </div>
            <div id="buttons">
                <span key="0" className="on"></span>
                <span key="1"></span>
                <span key="2"></span>
            </div>
        </div>
    )
}
